const Layout = () => import('@/layout/index')
const DashBoard = () => import('@/views/admin/dashboard/index');
const Login = () => import('@/views/admin/login')
const Page404 = () => import('@/views/admin/error-page/404.vue')
const Personal = () => import('@/views/admin/personal')
const DriverPage = () => import('@/views/admin/driver-page')
const ArticleAdd = () => import('@/views/admin/article/add-article')
const ArticleManage = () => import('@/views/admin/article/manage-article')
const ArticleDelete = () => import('@/views/admin/article/delete-article')
const ArticleDetail = () => import('@/views/admin/article/detail-article')
const Tag = () => import('@/views/admin/tag')
const Comment = () => import('@/views/admin/comment')
const User = () => import('@/views/admin/user')


export default [
    {
        path: '/admin/login',
        name: 'Login',
        component: Login,
        hidden: true
    },
    {
        path: '/admin/404',
        name: '404',
        component: Page404,
        hidden: true
    },
    {
        path: '/admin/',
        name: 'AdminHome',
        component: Layout,
        redirect: '/admin/index',
        children: [
            {
                path: 'index',
                name: 'Dashboard',
                component: DashBoard,
                meta: {title: '首页', icon: 'el-icon-s-data'}
            }
        ]
    },
    {
        path: '/admin/personal',
        name: 'Personal',
        component: Layout,
        redirect: '/admin/personal/index',
        children: [
            {
                path: 'index',
                name: 'Personal-home',
                component: Personal,
                meta: {title: '个人中心'},
                hidden: true
            }
        ]
    },
    {
        path: '/admin/driver',
        name: 'Driver',
        component: Layout,
        redirect: '/admin/driver/index',
        children: [
            {
                path: 'index',
                name: 'Driver-home',
                component: DriverPage,
                meta: {title: '引导指南', icon: 'el-icon-s-flag'}
            }
        ]
    },
    {
        path: '/admin/article',
        name: 'Article',
        component: Layout,
        redirect: '/admin/article/add',
        meta: {title: '文章', icon: 'el-icon-tickets'},
        children: [
            {
                path: 'add',
                name: 'Add article',
                component: ArticleAdd,
                meta: {title: '写文章', icon: 'el-icon-edit'},
            },
            {
                path: 'manage',
                name: 'Manage article',
                component: ArticleManage,
                meta: {title: '文章管理', icon: 'el-icon-s-management'},
            },
            {
                path: 'delete',
                name: 'Delete article',
                component: ArticleDelete,
                meta: {title: '回收站', icon: 'el-icon-delete'},
            },
        ]
    },
    {
        path: '/admin/article_detail',
        name: 'article_detail',
        component: Layout,
        redirect: '/admin/article_detail/index',
        children: [
            {
                path: 'index',
                name: 'article_detail',
                component: ArticleDetail,
                meta: {title: '文章详情'},
                hidden: true
            }
        ]
    },
    {
        path: '/admin/tag',
        name: 'Tag',
        component: Layout,
        redirect: '/admin/tag/index',
        children: [
            {
                path: 'index',
                name: 'Manage Tag',
                component: Tag,
                meta: {title: '分类/标签', icon: 'el-icon-collection-tag'}
            }
        ]
    },
    {
        path: '/admin/comment',
        name: 'Comment',
        component: Layout,
        redirect: '/admin/comment/index',
        children: [
            {
                path: 'index',
                name: 'Manage Comment',
                component: Comment,
                meta: {title: '评论', icon: 'el-icon-chat-line-round'}
            }
        ]
    },
    {
        path: '/admin/user',
        name: 'User',
        component: Layout,
        redirect: '/admin/user/index',
        children: [
            {
                path: 'index',
                name: 'Manage User',
                component: User,
                meta: {title: '用户', icon: 'el-icon-user'}
            }
        ]
    }
]