const BlogLayout = () => import('@/views/blog/layout')
const BlogHome = () => import('@/views/blog/home')
const BlogTag = () => import('@/views/blog/tag')
const BlogAbout = () => import('@/views/blog/about')
const BlogArchives = () => import('@/views/blog/archives')
const BlogArticle = () => import('@/views/blog/article')

export default [
    {
        path: '/',
        name: 'BlogHome',
        component: BlogLayout,
        redirect: '/home',
        children: [
            {
                path: 'home',
                name: 'Blog-Home',
                component: BlogHome
            }
        ]
    },
    {
        path: '/tag',
        name:'BlogTag',
        component: BlogLayout,
        redirect: '/tag/index',
        children: [
            {
                path: 'index',
                name: 'Blog-Tag',
                component: BlogTag
            }
        ]
    },
    {
        path: '/about',
        name:'BlogAbout',
        component: BlogLayout,
        redirect: '/about/index',
        children: [
            {
                path: 'index',
                name: 'Blog-About',
                component: BlogAbout
            }
        ]
    },
    {
        path: '/archives',
        name:'BlogArchives',
        component: BlogLayout,
        redirect: '/archives/index',
        children: [
            {
                path: 'index',
                name: 'Blog-Archives',
                component: BlogArchives
            }
        ]
    },
    {
        path: '/article',
        name:'BlogArticle',
        component: BlogLayout,
        redirect: '/article/index',
        children: [
            {
                path: 'index',
                name: 'Blog-Article',
                component: BlogArticle
            }
        ]
    }
]